.menu {
	padding-left: 0;
	a {
		display: block;
	}

	li {
		list-style: none;
	}

	.sub-menu {
		padding-left: 2rem;
	}

}

.navbar-brand {
	padding: 3px 0;
	.logo-svg {
		height: 100%;
		width: auto;
		display: block;
	}

	&:hover {
		.logo-path {
			fill: $menu-color;
		}
	}
}

.navbar-default {
	border-width: 0;
	font-size: 1.7rem;
	font-family: 'Dosis', sans-serif;
	text-transform: uppercase;

	a {
		position: relative;
		backface-visibility: hidden;
		transform: translateZ(0);
		text-shadow: 0 1px 1px darken($menu-bg, 10%);

		&:hover {
			&:before {
				right: 0;
			}
		}
	}
	.current-menu-item {
		background-color: darken($menu-bg, 10%);
		a {
			color: $accent-light;
		}
	}

	.navbar-nav {
		> .current_page_ancestor {
			background-color: darken($menu-bg, 10%);
			> a {
				background-color: darken($menu-bg, 10%);
				color: $accent-light;
			}
		}

		.menu-item {
			a {
				font-weight: 300;
			}
		}
	}

}

.navbar-nav {
	margin-top: 0;
	margin-bottom: 0;
	a {
		&:before {
			position: absolute;
			left: 0;
			right: 100%;
			top: 0;
			bottom: 0;
			background-color: darken($menu-bg, 10%);
			z-index: -1;
			transition: all 0.3s ease;
		}
	}
}

.dropdown-menu {
	font-size: 1.7rem;
	padding: 0;
	border: 1px solid darken($menu-bg, 10%);

	> li {
		> a {
			padding: 8px 20px;
		}
	}
}

.navbar-toggle {
	border: 0 none;
	margin-right: 0;
	padding: 9px 0;
	.icon-bar {
		width: 30px;
		+.icon-bar {
			margin-top: 5px;
		}
	}
}

.navbar-search-btn {
	line-height: 38px;
	color: $menu-color;
	font-size: 2rem;
	cursor: pointer;
    padding: 0;
    float: right;
    text-shadow: 0 1px 1px darken($menu-bg, 10%);

    .fa {
    	display: block;
    	line-height: 38px;
    }

	&:hover {
		.fa {
			color: $accent-light;
		}
	}
}

.navbar-collapse {
	max-height: 100vh;

