.archive, .search {
	.more-link {
		float: right;
	    clear: both;
	}

	.main {
		.btn:not(.search-submit) {
			margin-top: 1rem;
		}
	}

	.hentry {
		overflow: hidden;
	}
}

.images {
  height: 100%;
  @extend .transition;
}

.images, .twentytwenty-handle {
	opacity: 0;
	.visible & {
		opacity: 1;
	}
}

.img-compare {
	background-color: $heading-bg;
	border: 1px solid $body-bg;
}

.spinner {
  margin: 0 auto;
  width: 40px;
  height: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -20px;
  margin-top: -20px;
  text-align: center;

  //-webkit-animation: sk-rotate 2s infinite linear;
  animation: sk-rotate 2s infinite linear;
  .visible & {
  	z-index: -5;
  }
}

.dot1, .dot2 {
  width: 60%;
  height: 60%;
  display: inline-block;
  position: absolute;
  top: 0;
  background-color: $menu-bg;
  border-radius: 100%;

  -webkit-animation: sk-bounce 2s infinite ease-in-out;
  animation: sk-bounce 2s infinite ease-in-out;
}

.dot2 {
  top: auto;
  bottom: 0;
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}

@keyframes sk-rotate {
  100% {
    transform: rotate(360deg);
    }
  }


@keyframes sk-bounce {
  0%, 100% {
    transform: scale(0);
  } 50% {
    transform: scale(1);
  }
}